
import { defineComponent, reactive, ref } from 'vue';
import { notification } from 'ant-design-vue';
import { ArrowDownOutlined } from '@ant-design/icons-vue';

const cols = Array.from(Array(12).keys())
const rows = Array.from(Array(8).keys())

export default defineComponent({
  components: {
    ArrowDownOutlined
  },
  name:'barcode-96',
  emits: ['table-changed'],
  setup(props, context) {
    const position = ref<string>('0-0')
    const barcode_Lset = reactive({value:Array(12).fill('')})
    const barcode_Rset = reactive({value:Array(8).fill('')})
    let formInline = reactive({
      name: '',
      gene: '',
      // vector: '',
      group: '',
      // note: '',
      gRNA_PAM: ''
    } as {[key: string]: string})
    const confirmTableData = ref<{[key: string]: string}[]>([])
    const onAdd = () => {
      for (const ele in formInline) {
              if (formInline[ele] === '') {
                notification['warning']({
                  message: 'Alert',
                  description: 'Blank(s) are not allowed in the table.'
                })
              return
              }
            }
            confirmTableData.value.push(formInline)
            formInline = {
                name: '',
                gene: '',
                // vector: '',
                group: '',
                // note: '',
                gRNA_PAM: '',
                barcode_L: barcode_Lset.value[parseInt(position.value.split('-')[0])],
                barcode_R: barcode_Rset.value[parseInt(position.value.split('-')[1])]
            }
            context.emit('table-changed', confirmTableData)
    }
    return {
      position,
      cols,
      rows,
      radioStyle: {
        display: 'block',
        height: '30px',
        lineHeight: '30px',
      },
      barcode_Lset,
      barcode_Rset,
      formInline,
      onAdd
    };
  },
});
