
import { defineComponent } from 'vue'
import inputScene from './components/inputScene.vue'
import resultSceneo from './components/resultScene-overdisplayed.vue'
import ServiceWorker from './worker/service.worker'
import { clear } from 'idb-keyval'
import TestTable from './components/test-table.vue'
import inputForm from './components/inputform.vue'
import sampleinforinput from './components/sampleinforinput.vue'
import confirmTable from './components/confirmTable.vue'
import guidance from './components/guidance.vue'
import { InfoCircleOutlined,
        BookOutlined,
        FileAddOutlined,
        BarChartOutlined,
        PlusSquareOutlined,
        ScheduleOutlined,
        DashboardOutlined } from '@ant-design/icons-vue'
import Barcodes96 from './components/barcodes-96.vue'
import { notification } from 'ant-design-vue'
import Introduction from "@/components/introduction.vue";

export default defineComponent({
  name: 'App',
  data() {
    return {
      menu: {
        selected: ['1'],
        rootSubmenuKeys: ['2', '3', '4'],
        openKeys: [] as string[]
      },
      vmode: 0,
      debugmode: false,
      step: 0,
      fqmerged: false,
      inforTable: [] as {name: string, gene: string, group: string, gRNA_PAM: string, barcode_L: string, barcode_R: string}[],
      guidanceDrawer: false
    }
  },
  components: {
    Introduction,
    inputScene,
    TestTable,
    inputForm,
    sampleinforinput,
    confirmTable,
    resultSceneo,
    InfoCircleOutlined,
    BookOutlined,
    FileAddOutlined,
    BarChartOutlined,
    PlusSquareOutlined,
    ScheduleOutlined,
    DashboardOutlined,
    Barcodes96,
    guidance
},
  methods: {
    reload() {
      location.reload()
    },
    /**
     * menu control
     * @param index
     */
    handleMenuOpen(index: string[]) {
      const latestOpenKey = index.find(key => this.menu.openKeys.indexOf(key) === -1);
      if (this.menu.rootSubmenuKeys.indexOf(latestOpenKey!) === -1) {
        this.menu.openKeys = index
      } else {
        this.menu.openKeys = latestOpenKey ? [latestOpenKey] : [];
      }
      switch(latestOpenKey) {
        case '2':
          this.inforTable = []
          break
        case '3':
          this.vmode = 20
          break
        case '4':
          this.vmode = 30
      }
    },
    /**
     * menu control
     * @param index
     */
    handleMenuSelect(index: {[key: string]: any}) {
      switch(index.key) {
        case '1':
          this.vmode = 0
          break
        case '2-1':
          this.vmode = 11
          this.inforTable = []
          break
        case '2-2':
          this.vmode = 12
          this.inforTable = []
          break
        case '2-3':
          this.vmode = 13
          this.inforTable = []
          break
        case '3':
          this.vmode = 20
          break
        case '4':
          this.vmode = 30
      }
    },
    /**
     * connected with sample info input table
     * @param value
     */
    changetable(value: {name: string, gene: string, group: string, gRNA_PAM: string, barcode_L: string, barcode_R: string}[]) {
      const confirmchart = this.$refs.confirmt as any
      confirmchart.confirmTableData = value
      this.inforTable = value
    },
    /**
     * allocate workers for alignment
     */
    alignment() {
      this.menu.openKeys = ['4']
      clear() // clear idb-keyval parameter
      const input = this.$refs.input as any
      const confirmt = this.$refs.confirmt as any
      const genef = input.$refs.genetable.$refs.fileinput.files[0]
      const sampletable = JSON.parse(JSON.stringify(confirmt.confirmTableData))
      // check info table for blanks
      for (const line of sampletable) {
        for (const element in line) {
          if (line[element] == '') {
            notification['warning']({
              message: 'Alert',
              description: 'Blank(s) in the table may lead to unrecognized error. Please double check your sample info.'
            })
        return
          }
        }
      }
      const worker = new ServiceWorker('')
      if (this.fqmerged) {
        const file = input.$refs.fqfile0.$refs.fileinput.files[0]
        if (file==undefined || genef==undefined || confirmt.length === 0) {
          notification['warning']({
            message: 'Alert',
            description: 'Please select all required files!'
          })
          return
        }
        this.step = 1
        this.vmode = 30
        this.menu.openKeys = ['4']
        worker.postMessage({state: 4, file: file, table: sampletable, genef: genef, msg: 'Start'})
      } else {
        const files: {filea: File, fileb: File} = {filea:input.$refs.fqfile1.$refs.fileinput.files[0], fileb:input.$refs.fqfile2.$refs.fileinput.files[0]}
        if (!files.filea || !files.fileb || genef==undefined || confirmt.length === 0) {
          notification['warning']({
            message: 'Alert',
            description: 'Please select all required files!'
          })
          return
        }
        this.step = 1
        this.vmode = 30
        this.menu.openKeys = ['4']
        worker.postMessage({state: 2, files: files, table: sampletable, genef: genef, msg: 'Start'})
      }
      // render progress bar
      worker.onmessage = (event: any) => {
          const result = this.$refs.result as any
          if (event.data.state == 1 && event.data.file_strand == 'forward') {
            result.readingPg.percentage = parseFloat(event.data.progress.toFixed(2))
            if (result.readingPg.percentage == 100 && event.data.file_strand == 'forward') {
              result.readingPg.status = 'success'
            }
          } else if (event.data.state == 2) {
            result.alignmentPg.percentage = parseFloat(event.data.progress.toFixed(2))
            if (result.alignmentPg.percentage == 100) {
              result.alignmentPg.status = 'success'
            }
          } else if (event.data.state == 0) {
            result.scratchPg.percentage = parseFloat(event.data.progress.toFixed(2))
            if (result.scratchPg.percentage == 100) {
              result.scratchPg.status = 'success'
              this.step = 2
            }
          }
          if (event.data.state == 3) {
            //show res in table
            result.tableData = event.data.result
          }
        }
    },
    /**
     * push users to file input stage
     */
    sampleInfoConfirm() {
      this.vmode = 20
      this.menu.openKeys = ['3']
    }
  }
});
