
import { defineComponent } from 'vue'
import ServiceWorker from '@/worker/service.worker'
import { CopyOutlined } from '@ant-design/icons-vue'
import { notification } from 'ant-design-vue'

export default defineComponent({
    name: 'fileinput',
    components: {
        CopyOutlined
    },
    data() {
        return {
            file: undefined,
            fileName: ''
        }
    },
    emits: ['table-changed'],
    props:['indicate','ttip','infodata','infotable'],
    methods: {
        checkFile () {
            (this.$refs.fileinput as any).click()
        },
        checkFileSure () {
            this.fileName = (this.$refs.fileinput as any).files[0].name
            if (this.infodata == "true") {
                const worker = new ServiceWorker('')
                const file = (this.$refs.fileinput as any).files[0]
                worker.postMessage({state: 3, barcodetable: file, msg: 'Start'})
                worker.onmessage = (event: any) => {
                    if (event.data.state == 4) {
                        // check file format
                        if (event.data.table.length == 0) {
                            notification['warning']({
                                message: 'Alert',
                                description: 'Something wrong with the info table, please check your file.'
                            })
                        }
                        // check if blank exists
                        for (const line of event.data.table) {
                            for (const ele in line) {
                                if (line[ele] == '') {
                                    notification['warning']({
                                        message: 'Alert',
                                        description: 'Blank(s) are not allowed in the table.'
                                    })
                                    break
                                }
                            }
                        }
                        this.changetable(event.data.table)
                    }
                    worker.terminate()
                }
            }
        },
        changetable(table: {name: string, gene: string, group: string, gRNA_PAM: string, barcode_L: string, barcode_R: string}[]) {
            this.$emit('table-changed', table)
        }
    }
})
