
import { defineComponent } from 'vue'
import fileinput from './fileinput.vue'
import xlsx from 'xlsx'
import { QuestionOutlined } from '@ant-design/icons-vue'

export default defineComponent({
    name: 'sampleinforinput',
    components: {
      fileinput,
      QuestionOutlined
    },
    methods: {
      download_sample() {
        const workbook = xlsx.utils.book_new()
        workbook.Props= {
          Title: 'CrisprStitch Example',
          Subject: 'Example',
          Author: 'hanys'
        }
        workbook.SheetNames.push('1')
        const wsdata = [['Sample', 'Barcode_L', 'Barcode_R', 'gRNA_PAM', 'Gene', 'Group'],
          ['TX-2', 'TTAGGC', 'TGACCA', 'tttgGAGTGAAATCTCTTGTCTTAAGG', 'OsPDS-site01', 'pYPQ203-AsCpf1-OsPDS-crRNA01']]
        workbook.Sheets['1'] = xlsx.utils.aoa_to_sheet(wsdata)
        xlsx.writeFile(workbook, 'crisprstitch_example.xlsx')
      }
    }
})
