
import { defineComponent } from 'vue'
import ServiceWorker from '../worker/service.worker'
import bioseq from 'bioseq'

export default defineComponent({
  name: 'testTable',
  data() {
    return {
      readingPg: {
        percentage: 0,
        status: ''
      },
      inputseq1: '',
      inputseq2: '',
      alignmentPg: {
        percentage: 0,
        status: ''
      }
    }
  },
  methods: {
    start_sewing() {
      // merge
      const files: {filea: File, fileb: File} = {filea:(this.$refs.fqfile1 as any).files[0], fileb:(this.$refs.fqfile2 as any).files[0]}
      if (!files.filea || !files.fileb) {
        alert('Please select file!')
        return
      }
      const worker = new ServiceWorker('')
      worker.postMessage({state: 0, files: files, msg: 'Start'});
      worker.onmessage = (event: any) => {
        if (event.data.state == 1 && event.data.file_strand == 'forward') {
          this.readingPg.percentage = parseFloat(event.data.progress.toFixed(2))
          if (this.readingPg.percentage == 100 && event.data.file_strand == 'forward') {
            this.readingPg.status = 'success'
          }
        }
      }
    },
    split() {
      // mergesample split
      const sampleinfof = (this.$refs.barcodetable as any).files[0]
      const files: {filea: File, fileb: File} = {filea:(this.$refs.fqfile1 as any).files[0], fileb:(this.$refs.fqfile2 as any).files[0]}
      if (!files.filea || !files.fileb || !sampleinfof) {
        alert('Please select all required file!')
        return
      }
      const worker = new ServiceWorker('')
      worker.postMessage({state: 1, files: files, sampleinfo: sampleinfof, msg: 'Start'});
      worker.onmessage = (event: any) => {
        if (event.data.state == 1 && event.data.file_strand == 'forward') {
          this.readingPg.percentage = parseFloat(event.data.progress.toFixed(2))
          if (this.readingPg.percentage == 100 && event.data.file_strand == 'forward') {
            this.readingPg.status = 'success'
          }
        }
      }
    },
    alignment() {
      // mergesplitalignment
      const genef = (this.$refs.genetable as any).files[0]
      const sampletable = (this.$refs.sampletable as any).files[0]
      const barcodetable = (this.$refs.barcodetable as any).files[0]
      const files: {filea: File, fileb: File} = {filea:(this.$refs.fqfile1 as any).files[0], fileb:(this.$refs.fqfile2 as any).files[0]}
      if (!files.filea || !files.fileb || !barcodetable || !sampletable) {
        alert('Please select all required file!')
        return
      }
      const worker = new ServiceWorker('')
      worker.postMessage({state: 2, files: files, barcodetable: barcodetable, sampletable: sampletable, genef: genef, msg: 'Start'});
      worker.onmessage = (event: any) => {
        if (event.data.state == 1 && event.data.file_strand == 'forward') {
          this.readingPg.percentage = parseFloat(event.data.progress.toFixed(2))
          if (this.readingPg.percentage == 100 && event.data.file_strand == 'forward') {
            this.readingPg.status = 'success'
          }
        } else if (event.data.state == 2) {
          this.alignmentPg.percentage = parseFloat(event.data.progress.toFixed(2))
          if (this.alignmentPg.percentage == 100) {
            this.alignmentPg.status = 'success'
          }
        }
      }
    },
    aligntest() {
      const [seq1, seq2] = [this.inputseq1, this.inputseq2]
      var rst = bioseq.align(seq1, seq2, false);

      console.log("Score: ", rst.score);
      console.log("Starting position: ", rst.position);
      console.log("CIGAR: ", bioseq.cigar2str(rst.CIGAR));

      var fmt = bioseq.cigar2gaps(seq1, seq2, rst.position, rst.CIGAR);

      console.log(fmt[0]);
      console.log(fmt[1]);
      var str = bioseq.cigar2str(seq1, seq2, rst.position, rst.CIGAR)
      console.log(str)
    }
  }
})
