
import { defineComponent } from 'vue'
import { get, getMany } from 'idb-keyval'
import * as echarts from 'echarts'
import { zip } from '@/js/sampletable'
import JSZip from 'jszip'
import xlsx from 'xlsx'
import { Sample } from '@/js/sampletable'
import infiniteScroll from 'vue3-infinite-scroll-good'
import { AlignmentResult } from '@/js/alignment_result_drawing'
import * as pdfMake from "pdfmake/build/pdfmake"
import * as pdfFonts from 'pdfmake/build/vfs_fonts'
(pdfMake as any).vfs = pdfFonts.pdfMake.vfs

export default defineComponent({
    directives: {
      infiniteScroll
    },
    name: 'resultSceneo',
    data() {
        return {
          noMore: false,
          scrollProgress: 1,
          scrollLoad: false,
          radio: {} as {[key: string]: string},
          readingPg: {
              percentage: 0,
              status: 'active'
          },
          alignmentPg: {
              percentage: 0,
              status: 'active'
          },
          scratchPg: {
            percentage: 0,
            status: 'active'
          },
          showedData: [] as string[],
          tableData: [] as string[],
          groupTitle: '',
          centerDialogVisible: false,
          multi_align_res: [] as {
            sample: Sample, 
            res: {
              query: string, 
              count: number, 
              insertions: {
                index: number, 
                sequence: string
              }[]
            }[]
          }[],
          reference: '',
          loading: true,
          downloadloading: false,
          isBarChart: {} as {[key: string]: boolean}
        }
    },
    methods: {
      infiniteScroll() {
        if (this.tableData.length > this.scrollProgress - 1) {
          this.showedData = this.tableData.slice(0,this.scrollProgress)
          const scrollData = this.tableData[this.scrollProgress - 1]
          this.radio[scrollData] = 'Deletion'
          this.isBarChart[scrollData] = true
          get(scrollData).then(val => {
            this.groupTitle = scrollData
            const chartIns = echarts.init(document.getElementById('deletion-chart-'+scrollData) as HTMLElement, undefined, {renderer: 'svg'})
            new AlignmentResult(scrollData)
            this.chartData(JSON.parse(val).svgjson, this.isBarChart[scrollData], chartIns, 'deletionChart')
          })
          this.scrollProgress += 1
        } else {
          this.noMore = true
        }
      },
      /**
       * prepare data for download
       */
      downloadAll() {
        this.downloadloading = true
        const zipfile = new JSZip()
        const mutrate_overview = xlsx.utils.book_new()
        // adjust query format
        mutrate_overview.Props = {
          Title: 'Mutation rate overview',
          Subject: 'overview',
          Author: 'CrisprStitch'
        }
        mutrate_overview.SheetNames.push('overview')
        const echartsdiv = document.getElementById('chart-for-download') as HTMLElement
        const chartIns = echarts.init(echartsdiv, undefined, {renderer: 'svg'})
        const overview_data = [['Sample', 'mutation', 'substitution', 'insertion only', 'deletion only', 'insertion&deletion', 'mutation count', 'substitution count', 'insertion only count', 'deletion only count', 'insertion&deletion', 'total read count']]
        getMany(this.tableData).then(val => {
          const pdfReady = new Promise<void>((resolve, reject) => {
            const pdfStatus = new Array(10).fill(false)
            for (const [groupname, content] of zip(this.tableData, val)) {
              const groupfolder = zipfile.folder(groupname)
              this.multi_align_res = JSON.parse(content).sample
              for (const re of this.multi_align_res) {
                groupfolder.file(re.sample.name + '.txt', this.multiAlignResToString(re))
                overview_data.push(this.overviewSetup(re))
              }
              try {
                this.chartData(JSON.parse(content).svgjson, true, chartIns, 'deletionChart')
                pdfMake.createPdf({ content: [{ svg: chartIns.renderToSVGString() }], pageSize: { width: 1200, height: 'auto' } }).getBase64(result => {
                  groupfolder.file(groupname + '_deletion_bar.pdf', result, {base64: true})
                  pdfStatus[0] = true
                })
                this.chartData(JSON.parse(content).svgjson, true, chartIns, 'mutationChart')
                pdfMake.createPdf({ content: [{ svg: chartIns.renderToSVGString() }], pageSize: { width: 1200, height: 'auto' } }).getBase64(result => {
                  groupfolder.file(groupname + '_substitution_bar.pdf', result, {base64: true})
                  pdfStatus[1] = true
                })
                this.chartData(JSON.parse(content).svgjson, true, chartIns, 'deletionSizeChart')
                pdfMake.createPdf({ content: [{ svg: chartIns.renderToSVGString() }], pageSize: { width: 1200, height: 'auto' } }).getBase64(result => {
                  groupfolder.file(groupname + '_deletion_size_bar.pdf', result, {base64: true})
                  pdfStatus[2] = true
                })
                this.chartData(JSON.parse(content).svgjson, false, chartIns, 'deletionChart')
                pdfMake.createPdf({ content: [{ svg: chartIns.renderToSVGString() }], pageSize: { width: 1200, height: 'auto' } }).getBase64(result => {
                  groupfolder.file(groupname + '_deletion_line.pdf', result, {base64: true})
                  pdfStatus[3] = true
                })
                this.chartData(JSON.parse(content).svgjson, false, chartIns, 'mutationChart')
                pdfMake.createPdf({ content: [{ svg: chartIns.renderToSVGString() }], pageSize: { width: 1200, height: 'auto' } }).getBase64(result => {
                  groupfolder.file(groupname + '_substitution_line.pdf', result, {base64: true})
                  pdfStatus[4] = true
                })
                this.chartData(JSON.parse(content).svgjson, false, chartIns, 'deletionSizeChart')
                pdfMake.createPdf({ content: [{ svg: chartIns.renderToSVGString() }], pageSize: { width: 1200, height: 'auto' } }).getBase64(result => {
                  groupfolder.file(groupname + '_deletion_size_line.pdf', result, {base64: true})
                  pdfStatus[5] = true
                })
                this.chartData(JSON.parse(content).svgjson, true, chartIns, 'insertionChart')
                pdfMake.createPdf({ content: [{ svg: chartIns.renderToSVGString() }], pageSize: { width: 1200, height: 'auto' } }).getBase64(result => {
                  groupfolder.file(groupname + '_insertion_bar.pdf', result, {base64: true})
                  pdfStatus[6] = true
                })
                this.chartData(JSON.parse(content).svgjson, true, chartIns, 'insertionSizeChart')
                pdfMake.createPdf({ content: [{ svg: chartIns.renderToSVGString() }], pageSize: { width: 1200, height: 'auto' } }).getBase64(result => {
                  groupfolder.file(groupname + '_insertion_size_bar.pdf', result, {base64: true})
                  pdfStatus[7] = true
                })
                this.chartData(JSON.parse(content).svgjson, false, chartIns, 'insertionChart')
                pdfMake.createPdf({ content: [{ svg: chartIns.renderToSVGString() }], pageSize: { width: 1200, height: 'auto' } }).getBase64(result => {
                  groupfolder.file(groupname + '_insertion_line.pdf', result, {base64: true})
                  pdfStatus[8] = true
                })
                this.chartData(JSON.parse(content).svgjson, true, chartIns, 'insertionSizeChart')
                pdfMake.createPdf({ content: [{ svg: chartIns.renderToSVGString() }], pageSize: { width: 1200, height: 'auto' } }).getBase64(result => {
                  groupfolder.file(groupname + '_insertion_size_line.pdf', result, {base64: true})
                  pdfStatus[9] = true
                })
              } catch (e) {
                console.log(e)
                reject()
              }
            }
            const interval = setInterval(() => {
              // check if all pdf file ready
              if (pdfStatus.every(val => val)) {
                clearInterval(interval)
                resolve()
              }
            }, 1000)
          })
          pdfReady.then(() => {
            mutrate_overview.Sheets['overview'] = xlsx.utils.aoa_to_sheet(overview_data)
            zipfile.file('mut_rate_all.xlsx', xlsx.write(mutrate_overview, {type: 'base64'}), {base64: true})
            zipfile.generateAsync({type:"base64"}).then((base64: string) => {
              const element = document.createElement('a')
              element.setAttribute('href', 'data:application/zip;base64,' + base64)
              element.setAttribute('download', 'crisprstitch_result')
              element.style.display = 'none'
              element.click()
            })
          }).catch(() => {
            console.log('Error in generating pdf')
          })
        })
        setTimeout(() => {
          this.downloadloading = false
        }, 500);
      },
      showChart(group: string, isBar: boolean) {
        this.groupTitle = group
        const chartIns = echarts.init(document.getElementById('deletion-chart-'+group) as HTMLElement, undefined, {renderer: 'svg'})
        const alignment_result = document.getElementById('alignment-result-'+group) as HTMLElement
        this.loading = true
        setTimeout(() => {
          // set timeout to load 'loading' animation
          get(group).then(val => {
            switch(this.radio[group]) {
              case 'Deletion':
                this.chartData(JSON.parse(val).svgjson, isBar, chartIns, 'deletionChart')
                alignment_result.style.display = 'block'
                break
              case 'Substitution':
                this.chartData(JSON.parse(val).svgjson, isBar, chartIns, 'mutationChart')
                alignment_result.style.display = 'block'
                break
              case 'Deletion Size':
                this.chartData(JSON.parse(val).svgjson, isBar, chartIns, 'deletionSizeChart')
                alignment_result.style.display = 'none'
                break
              case 'Insertion':
                this.chartData(JSON.parse(val).svgjson, isBar, chartIns, 'insertionChart')
                alignment_result.style.display = 'block'
                break
              case 'Insertion Size':
                this.chartData(JSON.parse(val).svgjson, isBar, chartIns, 'insertionSizeChart')
                alignment_result.style.display = 'none'

          }
        })
        }, 500)
      },
      multiAlignResToString(res: {sample: Sample, res: {query: string, count: number, insertions:{index: number, sequence: string}[]}[]}) {
        let result = ''
        result += `${'-'.repeat(30)}${res.sample.gRNA_PAM}${'-'.repeat(30)}\n`
        result += `${res.sample.ref}\n`
        for (const line of res.res) {
          result += `${line.query} count: ${line.count} `
          for (const insertion of line.insertions) {
            result += `index: ${insertion.index} sequence: ${insertion.sequence} `
          }
          result += '\n'
        }
        return result
      },
      chartPreparation(group: string, name: string, xaxis: any[], data: number[], error: number[][] | undefined, isBarChart: boolean, chartIns: echarts.ECharts) {
        let option = {
          color: ['black'],
          title: {
            text: name,
            left: 'center'
          },
          animation: false,
          tooltip: error != undefined ? {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            },
            formatter: function(param: any) {
              const bar = param[0]
              const error = param[1]
              return `
                avg: ${bar.data}
                error: ${error.data[1] - bar.data}
                `
            }
          } : {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            },
            formatter: function(param: any) {
              const bar = param[0]
              return `
                value: ${bar.data}
                `
            }
          },
          xAxis: {
              axisLabel: {
                interval: 0,
                show: true,
                textStyle: {
                  fontSize: 14,
                  color: 'black'
                }
              },
              data: xaxis,
              axisTick: {
                alignWithLabel: true
              }
          },
          yAxis: {
              nameLocation: 'middle',
              nameGap: 50,
              nameTextStyle: {
                fontSize: 14,
                color: 'black'
              },
              axisLabel: {
                show: true,
                textStyle: {
                  fontSize: 14,
                  color: 'black'
                }
              },
              name: name.endsWith('Size')? 'count' : 'percentage'
          },
          grid: {
            left: '5%',
            right: '5%',
            bottom: '5%'
          },
          series: error === undefined ? [
            {
              data: data,
              type: isBarChart? 'bar':'line',
              name: isBarChart? 'bar':'line',
              areaStyle: {
                color: name.endsWith('Size')?'rgb(239, 148, 118)':'rgb(152, 235, 232)'
              },
              itemStyle: {
                color: name.endsWith('Size')?'rgb(249, 165, 19)':'rgb(88, 202, 224)'
              },
              smooth: true
            }
          ] : isBarChart ? [
            {
              data: data,
              type: 'bar',
              name: 'bar',
              areaStyle: {
                color: name.endsWith('Size')?'rgb(239, 148, 118)':'rgb(152, 235, 232)'
              },
              itemStyle: {
                color: name.endsWith('Size')?'rgb(249, 165, 19)':'rgb(88, 202, 224)'
              },
              smooth: true
            },
            {
              type: 'custom',
              name: 'error',
              itemStyle: {
                borderWidth: 1.5
              },
              renderItem: function (params: any, api: any) {
                const xValue = params.dataIndex
                const highPoint = api.coord([xValue, api.value(1)])
                const lowPoint = api.coord([xValue, api.value(2)])
                const halfWidth = (api.size([1, 0]) as number[])[0] * 0.2
                const style = api.style({
                  stroke: api.visual('color') as string,
                  fill: undefined,
                  color: name.endsWith('Size')?'rgb(249, 165, 19)':'rgb(88, 202, 224)'
                })

                return {
                  type: 'group',
                  children: [
                    {
                      type: 'line',
                      transition: ['shape'],
                      shape: {
                        x1: highPoint[0] - halfWidth,
                        y1: highPoint[1],
                        x2: highPoint[0] + halfWidth,
                        y2: highPoint[1]
                      },
                      style: style
                    },
                    {
                      type: 'line',
                      transition: ['shape'],
                      shape: {
                        x1: highPoint[0],
                        y1: highPoint[1],
                        x2: lowPoint[0],
                        y2: lowPoint[1]
                      },
                      style: style
                    },
                    {
                      type: 'line',
                      transition: ['shape'],
                      shape: {
                        x1: lowPoint[0] - halfWidth,
                        y1: lowPoint[1],
                        x2: lowPoint[0] + halfWidth,
                        y2: lowPoint[1]
                      },
                      style: style
                    }
                  ]
                }
              },
              encode: {
                x: 0,
                y: [1, 2]
              },
              data: error,
              z: 100
            },{
              type: 'line',
              smooth: true,
              data: error.map(val => val[2]),
              lineStyle: {
                width: 0
              },
              symbolSize: 0
            }
          ] : [
            {
              data: data,
              type: 'line',
              name: 'line',
              areaStyle: {
                color: name.endsWith('Size')?'rgb(239, 148, 118)':'rgb(152, 235, 232)'
              },
              itemStyle: {
                color: name.endsWith('Size')?'rgb(249, 165, 19)':'rgb(88, 202, 224)'
              },
              smooth: true
            },
            {
              type: 'line',
              smooth: true,
              data: error.map(val => val[1]),
            },
            {
              type: 'line',
              smooth: true,
              data: error.map(val => val[2]),
              lineStyle: {
                width: 2
              },
              symbolSize: 4
            }
          ]
        }
        option && chartIns.setOption(option)
        setTimeout(() => {
          chartIns.resize()
        }, 100)
        window.addEventListener('resize', () => {chartIns.resize()})
        const a = window.document.querySelector('#saveassvg-'+group) as HTMLAnchorElement
        a!.addEventListener('click',()=>{
          const content = document.getElementById('deletion-chart-'+group)!.outerHTML
          const clean_content = content.split(/(?=<svg)/g)[1].split(/(?<=<\/svg>)/g)[0]
          const blob= new Blob([clean_content], {type: 'xml/svg'})
          a!.href = URL.createObjectURL(blob)
          a!.download = `${group}_${name}.svg`

        })
      },
      chartData(chartInfo: any, isBar: boolean, chartIns: echarts.ECharts, chartType: 'deletionChart' | 'mutationChart' | 'deletionSizeChart' | 'insertionChart' | 'insertionSizeChart') {
        const group = chartInfo.groupsvgjson
        const xAxis = chartInfo[chartType].xaxis
        const bar = chartInfo[chartType].bar
        const errorBar = chartInfo[chartType].errorbar
        const chartName = {deletionChart: 'Deletion', mutationChart: 'Substitution', deletionSizeChart: 'Deletion Size', insertionChart: 'Insertion', insertionSizeChart: 'Insertion Size'}
        this.chartPreparation(group, chartName[chartType], xAxis, bar, errorBar, isBar, chartIns)
        this.loading = false
      },
      saveTXT(res: {sample: Sample, res: {query: string, count: number, insertions:{index: number, sequence: string}[]}[]}[]) {
        for (const re of res) {
          const element = document.createElement('a')
          element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(this.multiAlignResToString(re)))
          element.setAttribute('download', re.sample.name)
          element.style.display = 'none'
          element.click()
        }
      },
      overviewSetup(data: {sample: Sample, res: {query: string, count: number, insertions: {index: number, sequence: string}[]}[]}) {
        const tc = data.sample.sumup as number
        let [mc, rc, ioc, doc, idc] = [0,0,0,0,0]
        for (const re of data.res) {
          if (re.query === data.sample.ref && re.insertions.length == 0) {
            mc = tc - re.count
          } else {
            if (re.query.match(/a|t|c|g/)) {
              rc += re.count
            }
            if (re.query.match('-')) {
              if (re.insertions.length == 0) {
                doc += re.count
              } else {
                idc += re.count
              }
            } else if (re.insertions.length != 0) {
              ioc += re.count
            }
          }
        }
        if (mc == 0) mc = tc
        const [mr, rr, ior, dor, idr] = [mc/tc*100, rc/tc*100, ioc/tc*100, doc/tc*100, idc/tc*100]
        return [data.sample.name, mr, rr, ior, dor, idr, mc, rc, ioc, doc, idc, tc].map(column => column.toString())
      }
    }
})
