
import {defineComponent, computed} from "vue"

export default defineComponent({
  name: 'guidance',
  props: {
    visible: {
      type: Boolean,
      required: true,
      default: false
    }
  },
  emits: {
    "update:visible"(payload: boolean) {
      return payload !== undefined
    }
  },
  data() {
    return {
      showDrawer: computed({
        get: () => this.visible,
        set: (val) => {
          this.$emit("update:visible", val)
        }
      })
    }
  }
})
