
import { defineComponent } from 'vue'
import { notification } from 'ant-design-vue'

export default defineComponent({
    name: 'inputForm',
    data() {
        return {
            formInline: {
                name: '',
                gene: '',
                group: '',
                // note: '',
                gRNA_PAM: '',
                barcode_L: '',
                barcode_R: ''
            } as {[key: string]: string},
            confirmTableData: [] as {[key: string]: string}[]
        }
    },
    emits:['table-changed'],
    methods: {
        onAdd() {
            for (const ele in this.formInline) {
              if (this.formInline[ele] === '') {
                notification['warning']({
                  message: 'Alert',
                  description: 'Blank(s) are not allowed in the table.'
                })
              return
              }
            }
            this.confirmTableData.push(this.formInline)
            this.formInline = {
                name: '',
                gene: '',
                group: '',
                // note: '',
                gRNA_PAM: '',
                barcode_L: '',
                barcode_R: ''
            }
            this.$emit('table-changed', this.confirmTableData)
        }
    }
})
