  {
    title: 'Sample',
    dataIndex: 'name',
    width: '150',
    slots: {customRender: 'name'}
  },
  {
    title: 'Gene',
    dataIndex: 'gene',
    width: '120',
    slots: {customRender: 'gene'}
  },
  // {
  //   title: 'Note',
  //   dataIndex: 'note',
  //   width: '300',
  //   slots: {customRender: 'note'}
  // },
  // {
  //   title: 'Vector',
  //   dataIndex: 'vector',
  //   width: '120',
  //   slots: {customRender: 'vector'}
  // },
  {
    title: 'Group',
    dataIndex: 'group',
    width: '280',
    slots: {customRender: 'group'}
  },
  {
    title: 'gRNA_PAM',
    dataIndex: 'gRNA_PAM',
    width: '120',
    slots: {customRender: 'gRNA_PAM'}
  },
  {
    title: 'Barcode_L',
    dataIndex: 'barcode_L',
    width: '120',
