    slots: {customRender: 'operation'}
  }
]

interface DataItem {
  name: string
  gene: string
  // vector: string
  group: string
  note: string
  gRNA_PAM: string
  barcode_L: string
  barcode_R: string
}

const data: DataItem[] = []

export default defineComponent({
    name: 'confirmTable',
    setup(props, context) {
      const confirmTableData = ref(data)
      const editableData: UnwrapRef<Record<string, DataItem>> = reactive({})
      const edit = (key: string) => {
        editableData[key] = cloneDeep(confirmTableData.value.filter(item => key === item.name)[0])
      }
      const save = (key: string) => {
        Object.assign(confirmTableData.value.filter(item => item.name)[0], editableData[key])
        delete editableData[key]
      }
      const cancel = (key: string) => {
        delete editableData[key]
      }
      const deleteRow = (key: string) => {
        const index = confirmTableData.value.map(obj => obj.name).indexOf(key)
            confirmTableData.value.splice(index, 1)
            context.emit('table-changed', confirmTableData)
      }
      const onClear = () => {
        confirmTableData.value = []
        context.emit('table-changed', confirmTableData)
      }
      return {
        confirmTableData,
        columns,
        editingKey: '',
        editableData,
        edit,
        save,
        cancel,
        deleteRow,
        onClear
      }
    },
    emits: ['table-changed'],
    methods: {
        tableRowClassName(rowdata: any) {
          const row = JSON.parse(JSON.stringify(rowdata))
            for (const ele in row) {
                if (row[ele] == '') {
                    return 'litigationInfoListredRow'
                }
                if (ele == 'gRNA_PAM' || ele == 'barcode_L' || ele == 'barcode_R') {
                    if(row[ele].match(/^[A|a|T|t|C|c|G|g]+$/)) {
                        break
                    } else {
                        return 'litigationInfoListredRow'
                    }
                }
            }
            return ''
        }
    }
})
